<template>
  <section id="about">
    <div class="about-cards">
      <vs-row>
        <vs-col vs-w="12">
          <CurtainCard
            v-for="(card, index) in cardsCurtains"
            :key="index"
            v-bind:content="card"
          ></CurtainCard>
        </vs-col>
      </vs-row>
    </div>
    <div class="legend">
      <vs-row>
        <vs-col vs-w="12"></vs-col>
      </vs-row>
      <h2>LEGEND</h2>
      <div class="legend-card card-shadow">
        <div class="legend-card--text">
          <p>{{legendText}}}</p>
        </div>
      </div>
    </div>
    <div class="advantages-cards">
      <vs-row>
        <AdvantagesCard
          v-for="(card, index) in cardsAdvantages"
          :key="index"
          v-bind:content="card"
          v-bind:smaller="false">
        </AdvantagesCard>
      </vs-row>
    </div>
    <div class="slogan-mission">
      <SloganLine v-bind:content="sloganContent"></SloganLine>
    </div>
    <Footer></Footer>
  </section>
</template>

<script>
import CurtainCard from '@/components/aboutus/CurtainCard';
import AdvantagesCard from '@/components/AvantagesCard';
import SloganLine from '@/components/SloganLine';

export default {
  name: 'AboutUs',
  components: {
    CurtainCard,
    AdvantagesCard,
    SloganLine,
  },
  data() {
    return {
      sloganContent: {
        header: 'MISSION',
        text: 'Wybicie si z rutyny - ycie przepenione szaroci, chcemy napeni jaskrawymi barwami.',
      },
      cardsCurtains: [{
        id: 0,
        text: 'Na rynku odzieowym spotkalimy si z du liczb identycznych za swoim wygldem towarw. Rnokolorowe zdjcia, due rozmiary, hasa standardowo w centrum - typowy sposb realizacji. Wszystko to przyciga uwag, rzuca si w oczy. Chcielimy innego - wyrnia si niepozornie.',
        lighterUrl: '/static/about/lighter_problem.png',
      }, {
        id: 1,
        text: 'Na rynku odzieowym spotkalimy si z du liczb identycznych za swoim wygldem towarw. Rnokolorowe zdjcia, due rozmiary, hasa standardowo w centrum - typowy sposb realizacji. Wszystko to przyciga uwag, rzuca si w oczy. Chcielimy innego - wyrnia si niepozornie.',
        lighterUrl: '/static/about/lighter_idea.png',
      }, {
        id: 2,
        text: 'Na rynku odzieowym spotkalimy si z du liczb identycznych za swoim wygldem towarw. Rnokolorowe zdjcia, due rozmiary, hasa standardowo w centrum - typowy sposb realizacji. Wszystko to przyciga uwag, rzuca si w oczy. Chcielimy innego - wyrnia si niepozornie.',
        lighterUrl: '/static/about/lighter_solution.png',
      }],
      legendText: 'Wdrowiec natrafi na (ma) wiosk znajdujc si u podna gry. W miejscowej\n' +
        '            tawernie pozna mieszkaca tej wioski, mczyzn w podeszym wieku o imieniu Brevis.\n' +
        '            By on miejscowym bajarzem i podczas pobytu w tawernie opowiedzia wdrowcowi kilka\n' +
        '            ciekawych historii o jego kraju. Brevis by ubrany jak wszyscy, za wyjtkiem jego grnej\n' +
        '            odziey. Z wygldu ona bya bardzo stara, a od spodu widniay wyhaftowane obrazy jelenia\n' +
        '            i wilka. Zobaczywszy zainteresowanie swoim ubiorem opowiedzia histori ze swojego ycia.\n' +
        '            W modoci, zbierajc chrust, zosta zaatakowany przez wilka. W nierwnej walce\n' +
        '            straci on wszystkie siy i czeka na mier, a tu znikd pojawi si jele. Wystawiwszy\n' +
        '            poroe nastraszy wilka i zmusi go do rychej ucieczki. Na znak pamici tego dnia,\n' +
        '            Brevis pozostawi sobie zniszczon odzie, w ktrej by w tamtym dniu. Zaszywszy podarte\n' +
        '            miejsca obrazami dwch zwierzt, zawsze jest chtny do opowiedzenia wszystkim\n' +
        '            zainteresowanym histori o niespodziewanym ratunku.',
      cardsAdvantages: [{
        id: 0,
        img_url: 'static/about/loop.svg',
        header: 'GOD IN DETAILS',
        text: 'Gwnym powodem powstania Brevis Tale \n' +
          'posuy problem. W dzisiejszych czasach \n' +
          'jedni wyrniaj si wybierajc znane \n' +
          'marki, drudzy podaj za mod,',
      }, {
        id: 1,
        img_url: 'static/about/vesy.svg',
        header: 'GOD IN DETAILS',
        text: 'Gwnym powodem powstania Brevis Tale \n' +
          'posuy problem. W dzisiejszych czasach \n' +
          'jedni wyrniaj si wybierajc znane \n' +
          'marki, drudzy podaj za mod,',
      }, {
        id: 2,
        img_url: 'static/about/paintset.svg',
        header: 'GOD IN DETAILS',
        text: 'Gwnym powodem powstania Brevis Tale \n' +
          'posuy problem. W dzisiejszych czasach \n' +
          'jedni wyrniaj si wybierajc znane \n' +
          'marki, drudzy podaj za mod,',
      }],
    };
  },
};
</script>

<style lang="scss">
  @import "~@/styles/style.scss";
  .about-cards {
    margin-top: $block-margin;
  }
  .legend {
    h2 {
      font-size: 84px;
      margin-bottom: $basic-padding;
      color: $primary-color;
    }
    margin-top: $block-margin;
   &-card {
     background-color: white;
     height: 120%;
     padding: 50px $block-margin/2;
     margin: 0 50px;
     &--text {
       p {
         position: relative;
         display: block;
         margin-top: 50px;
         margin-bottom: 50px;
         &:before {
           content: '';
           background-image: url("../assets/about/left-quote.svg");
           position: absolute;
           left: -50px;
           top: -35px;
           height: 46px;
           width: 46px;
         }
         &:after {
           content: '';
           background-image: url("../assets/about/right-quote.svg");
           position: absolute;
           right: -50px;
           bottom: -35px;
           height: 46px;
           width: 46px;
         }
       }
       padding: $block-margin/2 $block-margin*1.5;
     }
    }
  }
  .advantages {
    margin-top: $block-margin;

    &-cards {
      width: 80%;
      margin: auto;
    }
    &-card {
      img {
        height: 100px;
        width: auto;
      }
    }
  }
  .slogan-mission {
    margin-top: $block-margin;
    background-color: white;
  }
</style>



