<template>
  <section class="homepage" id="Homepage">
    <div class="homepage-header">
      <vs-row>
        <vs-col vs-w="12">
          <img class="homepage-header--image left" src="../assets/deer.png" alt="">
          <h1 class="homepage-header--title">What is Brevis Tale?</h1>
          <vs-button color="danger" type="filled"
                     class="primary-button">FIND OUT</vs-button>
          <img class="homepage-header--image right" src="../assets/shirt.png" alt="">
        </vs-col>
      </vs-row>
    </div>
    <div class="homepage-content">
      <BasicSection
        v-for="(post, index) in posts"
        :key="index"
        v-bind:content="post"></BasicSection>
      <div class="homepage-cta">
        <vs-row vs-align="center">
          <vs-col s-type="flex" vs-w="12">
            <vs-button color="danger" type="filled"
                       class="primary-button">
              CHECK THE STORY
            </vs-button>
          </vs-col>
        </vs-row>
      </div>
      <Footer></Footer>
    </div>
  </section>
</template>

<script>
import BasicSection from '@/components/homepage/BasicSection';

export default {
  name: 'Homepage',
  components: {
    BasicSection,
  },
  data() {
    return {
      posts: [{
        id: 0,
        // eslint-disable-next-line no-template-curly-in-string
        img_url: 'static/home/photo0.png',
        text: 'Gwnym powodem powstania Brevis Tale posuy problem. W dzisiejszych czasach jedni wyrniaj si wybierajc znane marki, drudzy podaj za mod, trzeci oddaj przewag konkretnemu stylowi. Przyczyn pojawienia nas by inny problem, a mianowicie - wyrnia si niepozornie.',
        alignSide: 'right',
      }, {
        id: 1,
        // eslint-disable-next-line no-template-curly-in-string
        img_url: 'static/home/photo1.png',
        text: 'Analizujc rne marki nie moglimy znale kompletnego rozwizania tego problemu. Wikszo firm naszym zdaniem proponuj towary mao rnice si od innych. Zrozumielimy, e dla nas oni s mao oryginalni, nieciekawi i brakuje im iskry. Jednak to nie by koniec, szukalimy dalej.',
        alignSide: 'left',
      }, {
        id: 2,
        // eslint-disable-next-line no-template-curly-in-string
        img_url: 'static/home/photo2.png',
        text: 'Podczas poszukiwa natknlimy si na ciekaw histori. Wdrowiec natrafi na osiedlenie u podna gry. Tam zapozna si ze starcem o imieniu Brevis. Wyrnia si on od innych mieszkacw prostymi obrazami jelenia i wilka na jego odziey. Te obrazy byy dla niego pamitk z jednego dnia, kiedy jele uratowa go od wygodniaego wilka. Dla wszystkich to byy po prostu obrazy zwierzt na odziey, lecz dla Brevisa oni byy czym o wiele wikszym. Na podstawie tej historii u nas pojawia si idea.',
        alignSide: 'right',
      }, {
        id: 3,
        // eslint-disable-next-line no-template-curly-in-string
        img_url: 'static/home/photo3.png',
        text: 'Analizujc rne marki nie moglimy znale kompletnego rozwizania tego problemu. Wikszo firm naszym zdaniem proponuj towary mao rnice si od innych. Zrozumielimy, e dla nas oni s mao oryginalni, nieciekawi i brakuje im iskry. Jednak to nie by koniec, szukalimy dalej.',
        alignSide: 'left',
      }],
    };
  },
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss">
  @import "~@/styles/style.scss";
  .homepage{
    background-color: white;
    &-header {
      height: 100vh;

      &--title {
        margin-top: 240px;
      }

      &--button {
        height: 65px;
        font-size: 18px;
        font-weight: bold;
        padding: 21px 73px;
        margin-top: 50px;
      }

      &--image {
        position: absolute;

        &.right {
          top: 136px;
          right: 0;
        }

        &.left {
          top: 136px;
          left: 0;
        }
      }
    }
    &-cta {
      height: 540px;
      margin: 0 110px;
      background-color: $bg-color-grey;
      .vs {
        &-row {
          height: 100%;
        }
      }
      .homepage-header--button {
        margin: auto;
      }
    }
    .primary-button {
      height: 65px;
      font-size: 18px;
      font-weight: bold;
      padding: 21px 73px;
      margin-top: 50px;
    }
  }
</style>



